.image-slider {
      display: flex;
        overflow-x: auto;
          gap: 10px;
            padding: 1rem;
}

.image-slider img {
      height: 200px;
        width: 100%;
          object-fit: cover;
            border-radius: 8px;
              box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
                transition: transform 0.3s ease;
}

.image-slider img:hover {
      transform: scale(1.05);
}

@media (max-width: 768px) {
      .image-slider {
            flex-direction: column;
                align-items: center;
      }
}



